.listItem {
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: space-between;
}

.listItem:not(:last-child) {
	margin-bottom: 10px;
}

.contact {
	font-size: 16px;
}

.contact span {
	font-weight: 700;
}

.contactListButton {
	display: block;
	color: #fff;
	background-color: #3f51b5;
	border: 0;
	outline: 0;
	padding: 4px 8px;
	border-radius: 4px;
	cursor: pointer;
	box-shadow: 0px 2px 1px -1px rgba(0, 0, 0, 0.2), 0px 1px 1px 0px rgba(0, 0, 0, 0.14),
		0px 1px 3px 0px rgba(0, 0, 0, 0.12);
	font: 12px;
	text-transform: uppercase;
	user-select: none;
	transition: all 200ms ease-in-out;
}

.contactListButton:hover {
	box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.25), 0px 4px 8px rgba(0, 0, 0, 0.22);
	background-color: #3947a7;
}

